body {
  box-sizing: border-box;
  padding: 10px;
}


.content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center; 
  justify-content: flex-start;
}

.text-content {
    flex: 0 0 50%;
    padding-left: 0px;
    margin-bottom: 40px;
    margin-left: 0px;
    margin-top: 60px;
    text-align: left;
    max-width: 700px;
  }

.image-content img {
    max-width: 100%;
    background-color: transparent;
    height: auto; /* maintain aspect ratio */
    border-radius: 50%;
    box-shadow: 0 20px 20px 0 rgba(30, 28, 28, 0.2);
}


.text-content h2 {
  color: white;
  font-size: 30px;
  font-family: 'Roboto', sans-serif;
}

.image-content {
  display: flex;
  background-color: transparent;
  justify-content: center;
  align-items: center;
  max-width: 700px;
}
  
  .responsive-image {
    max-width: 100%; /* Ensures the image doesn't exceed its container's width */
    height: auto; /* Maintains the image's aspect ratio */
  }
  

.gradient-text {
  background: linear-gradient(to right, yellow, purple);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 50px;
  font-weight: bold;
}

.info-content {
  color: white;
  text-align: center;
  margin-left: 53px;
  margin-right: 35px
}

.margin-bottom {
  margin-bottom: 150px
}

.card-text {
  color: white;
}

.card {
  background-color: transparent;
  border: none;
}

  
  @media (max-width: 991px) {
    .text-content {
      flex: 1 0 100%; /* Full width on smaller screens */
      margin-left: 0; /* No left margin on smaller screens */
      text-align: left; /* Center text on smaller screens */
      white-space: normal; /* Allow text to wrap on smaller screens */
    }

    .image-content {
      display: flex;
      justify-content: center;
      align-items: left;
    }

    .text-content h2 {
      color: white;
      font-size: 30px;
      font-family: 'Roboto', sans-serif;
    }
  }
  

  @media (max-width: 767px) {
    .text-content {
      flex: 1 0 100%; /* Full width on smaller screens */
      margin-left: 0; /* No left margin on smaller screens */
      text-align: left; /* Center text on smaller screens */
      white-space: normal; /* Allow text to wrap on smaller screens */
    }

    .image-content {
      display: flex;
      justify-content: left;
      align-items: left;
    }

    .text-content h2 {
      color: white;
      font-size: 22px;
      font-family: 'Roboto', sans-serif;
    }
  }


nav {
    background-color: darkcyan;
    font: 20px "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-weight: bold;
    background: darkcyan;
  }
  
  .navbar li a:hover {
    font-weight: bold;
    background-color: #5D5484;
  }
  
  .navbar-light .navbar-nav .nav-link {
    color: white;
    font-weight: bold;
  }

  
  .dropdown-menu {
    min-width: 200px;
    background-color: #3a4b73;
    color: #ffffff;
    font-weight: bold;
  }
  
  .dropdown-menu.columns-2 {
    min-width: 400px;
  }
  
  .dropdown-menu.columns-3 {
    min-width: 600px;
  }
  
  .dropdown-menu li a {
    padding: 5px 15px;
    font-weight: 300;
  }
  
  .multi-column-dropdown {
    list-style: none;
    margin: 0px;
    padding: 0px;
  }
  
  .multi-column-dropdown li a {
    display: block;
    clear: both;
    white-space: normal;
  }
  
  .dropdown-menu.show a.dropdown-item:hover {
    background-color: #5D5484;
  }
  
  .dropdown-menu .dropdown-item,
  .dropdown-menu .dropdown-header {
    color: white;
  }
  
  .btn-check:active + .btn-secondary,
  .btn-check:checked + .btn-secondary,
  .btn-secondary.active,
  .btn-secondary:active,
  .show > .btn-secondary.dropdown-toggle,
  .transparent-button.btn.btn-secondary {
    color: #fff;
    background-color: transparent;
    border: none;
  }
  
  .navbar-light .navbar-nav .nav-link.active,
  .navbar-light .navbar-nav .show > .nav-link {
    color: #fff;
    border: none;
  }
  
  .dropdown-item.active,
  .dropdown-item:active {
    color: #fff;
    text-decoration: none;
    background-color: transparent;
  }

  .navbar-brand {
    display: flex;
    font-size: 60px;
    background: -webkit-linear-gradient(yellow, purple);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
    font-family:'Roboto';
  }


@media screen and (max-width: 991px) {
  nav {
    background-color: darkcyan;
    font: 18px "Helvetica Neue", Helvetica, Arial, sans-serif;
    background: darkcyan;
  }

  .navbar-brand {
    display: flex;
    font-size:30px;
    background: -webkit-linear-gradient(yellow, #8B008B);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
    font-family:'Roboto';
}
}

@media screen and (max-width: 767px) {
  nav {
    background-color: darkcyan;
    font: 16px "Helvetica Neue", Helvetica, Arial, sans-serif;
    background: darkcyan;
  }
  .navbar-brand {
    display: flex;
    font-size:35px;
    background: -webkit-linear-gradient(yellow, #8B008B);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
    font-family:'Roboto';
  }
}




